import { __ } from '@wordpress/i18n';
import React from 'react';
import Select2 from "react-select";
import { useEffect, useState, useRef } from "react";
import { InspectorControls, PanelColorSettings, ColorPalette} from '@wordpress/block-editor';
import { TextControl, BaseControl, ToggleControl, PanelBody, PanelRow, CheckboxControl, SelectControl, ColorPicker, Button , IconButton, Dashicon, RangeControl, TabPanel, QueryControls } from '@wordpress/components';
import apiFetch from '@wordpress/api-fetch';
import { addQueryArgs } from '@wordpress/url';
import { BoxShadowControl } from '/src/components/boxShadow.js';
import { TypographyControl } from '/src/components/typographyControl.js';
import { ResponsiveRangeControl } from '/src/components/responsiveRangeControl.js';
import { ResponsiveDimentionControl } from '/src/components/responsiveDimentionControl.js';
import { ResponsiveAlignControl } from '/src/components/responsiveAlignControl.js';
import { CustomBorderControl } from '/src/components/borderControl.js';
import { ColorPickerControl } from '/src/components/customColorPicker.js';
import { CustomGradientControl } from '/src/components/customGradientControl.js'; 
import {
	__experimentalToggleGroupControl as ToggleGroupControl,
	__experimentalToggleGroupControlOption as ToggleGroupControlOption,
  } from '@wordpress/components';

export default function inspector(props) {

		const [categoriesList, setCategoriesList] = useState([]);
		const [tagsList, setTagsList] = useState([]);
		const [authorsList, setAuthorsList] = useState([]);
		const [stillMounted, setStillMounted] = useState(false);
		const { attributes, setAttributes } = props;
		const [activeGeneralPanel, setActiveGeneralPanel] = useState('postSettings');
        const [activeStylePanel, setActiveStylePanel] = useState('postStyle');
        const [activeAdvancedPanel, setActiveAdvancedPanel] = useState('responsive');

        const toggleGeneralPanel = (panelName) => {
            setActiveGeneralPanel((prevPanel) => (prevPanel === panelName ? null : panelName));
        };

        const toggleStylePanel = (panelName) => {
            setActiveStylePanel((prevPanel) => (prevPanel === panelName ? null : panelName));
        };

		const toggleAdvancedPanel = (panelName) => {
            setActiveAdvancedPanel((prevPanel) => (prevPanel === panelName ? null : panelName));
        };
		const { hideDesktop,
				hideTablet,
				hideMobile,
				numberOfItem,
				columns,
				columnsGap,
				rowGap,
				checkPostImage, 
				checkPostAuthor, 
				checkPostDate,  
				checkPostAavtars,
				order,
				categoryArray, 
				orderBy, 
				checkPostComment,
				checkPostCat,
				authorArray,
				tagArray,
				contentPosition,
				postBgColor,
				postBorderType,
				postBorderRadius,
				postPadding,
				postInnerPadding,
				postShadow,
				postBgHoverColor,
				postHoverBorderType,
				postHoverBorderRadius,
				postHoverPadding,
				postInnerHoverPadding,
				postHoverShadow,
				overlayColor,
				overlayOpacity,
				postLargeImageHeight,
				postSmallImageHeight,
				postImageBorderType,
				postImageBorderRadius,
				postImageHoverBorderType,
				postImageHoverBorderRadius,
				categoryPosition,
				categoryPositionLeft,
				categoryPositionRight,
				categoryAlignment,
				categoryBgColor,
				categoryColor,
				categoryBorderType,
				categoryBorderRadius,
				categoryPadding,
				categoryMargin,
				categoryShadow,
				categoryTypography,
				categoryBgHoverColor,
				categoryHoverColor,
				categoryHoverBorderType,
				categoryHoverBorderRadius,
				categoryHoverPadding,
				categoryHoverMargin,
				categoryHoverShadow,
				categoryHoverTypography,
				titleAlignment,
				titleColor,
				titleTypography,
				titleTypographySmall,
				titleMargin,
				titleHoverColor,
				titleHoverMargin,
				metaAlignment,
				metaColor,
				metaIconColor,
				metaTypography,
				metaMargin,
				metaHoverColor,
				metaIconHoverColor,
				metaHoverMargin,
			} = attributes;

			useEffect(() => {
				setStillMounted(true);
				return () => setStillMounted(false);
			}, []);

			useEffect(() => {
				if (stillMounted) {
					apiFetch({ path: addQueryArgs("/wp/v2/categories", { per_page: -1 }) })
						.then((categoriesList) => {
							setCategoriesList(categoriesList);
						})
						.catch(() => {
							if (stillMounted) {
								setCategoriesList([]);
							}
						});
				}

				apiFetch({ path: addQueryArgs("/wp/v2/tags", { per_page: -1 }) })
				.then((tagsList) => {
					setTagsList(tagsList);
				})
				.catch(() => {
					if (stillMounted) {
						setTagsList([]);
					}
				});

				apiFetch({
					path: addQueryArgs("/wp/v2/users", { per_page: -1, who: "authors" }),
				})
					.then((authorsList) => {
						setAuthorsList(authorsList);
					})
					.catch(() => {
						if (stillMounted) {
							setAuthorsList([]);
						}
					});
			}, [stillMounted]);

			const queryControlPanel = QueryControls.toString().includes(
				"selectedCategories"
			) ? (
				<div className='ananta-control-label' style={{marginBottom: "20px"}}>
					<QueryControls
					{...{ order, orderBy }}
						onOrderChange={(value) => setAttributes({ order: value })}
						onOrderByChange={(value) => setAttributes({ orderBy: value })}
					/>
				</div>
			) : (
				<div className='ananta-control-label'>
					<QueryControls
					{...{ order, orderBy }}
						onOrderChange={(value) => setAttributes({ order: value })}
						onOrderByChange={(value) => setAttributes({ orderBy: value })}
					/>
				</div>
			);

		const onSelect = ( tabName ) => {
			console.log( 'Selecting tab', tabName );
		};
  return (
	<InspectorControls key="inspector">
		<div className='ananta-panel-control'>
							<TabPanel
							className="ananta-tab-panel"
							activeClass="active-tab"
							onSelect={ onSelect }
							tabs={ [
								{
									name: 'general',
									title: 'General',
									className: 'ananta-tab general',
								},
								{
									name: 'style',
									title: 'Style',
									className: 'ananta-tab style',
								},
								{
									name: 'advanced',
									title: 'Advanced',
									className: 'ananta-tab advanced',
								},
							] }
						>
							{ ( tab ) => 
								<div className= {"ananta-tab-controls " + tab.name }>
									{ 
									"general" === tab.name && 
									<>
									<PanelBody
									title={__("Post Settings", "ananta-sites")} 
									opened={activeGeneralPanel === 'postSettings'}
									onToggle={() => toggleGeneralPanel('postSettings')}
									>
                                        <RangeControl
											className='ananta-range-label'
											label={__("Number of Posts", "ananta-sites")}
											value={ numberOfItem }
											onChange={(value) => setAttributes({ numberOfItem: value })}
											min={1}
											max={100}
                                        />
										<RangeControl
											className='ananta-range-label'
											label={__("Columns", "ananta-sites")}
											value={ columns }
											onChange={(value) => setAttributes({ columns: value })}
											min={1}
											max={3}
                                        />
										<ResponsiveRangeControl
											values={columnsGap}
											onChange={(value) => {
												setAttributes({
													columnsGap: { ...value },
												});
											}}
											label="Column Space"
											min={0}
											max={100}
                                		/>
										<ResponsiveRangeControl
											values={rowGap}
											onChange={(value) => {
												setAttributes({
													rowGap: { ...value },
												});
											}}
											label="Row Space"
											min={0}
											max={100}
                                		/>
										<ToggleControl
											className="ananta-control-label"
											label={__("Display Featured Image", "ananta-sites")}
											checked={checkPostImage}
											onChange={(checkPostImage) => setAttributes({ checkPostImage })}
										/>
										<ToggleControl
											className="ananta-control-label"
											label={__("Display Author", "ananta-sites")}
											checked={checkPostAuthor}
											onChange={(checkPostAuthor) => setAttributes({ checkPostAuthor })}
										/>
										<ToggleControl
											className="ananta-control-label"
											label={__("Display Date", "ananta-sites")}
											checked={checkPostDate}
											onChange={(checkPostDate) => setAttributes({ checkPostDate })}
										/>
										<ToggleControl
											className="ananta-control-label"
											label={__("Display Aavtar", "ananta-sites")}
											checked={checkPostAavtars}
											onChange={(checkPostAavtars) => setAttributes({ checkPostAavtars })}
										/>
										<ToggleControl
											className="ananta-control-label"
											label={__("Display Category", "ananta-sites")}
											checked={checkPostCat}
											onChange={(checkPostCat) => setAttributes({ checkPostCat })}
										/>
										<ToggleControl
											className="ananta-control-label"
											label={__("Display Comments", "ananta-sites")}
											checked={checkPostComment}
											onChange={(checkPostComment) => setAttributes({ checkPostComment })}
										/>
									</PanelBody>
									<PanelBody
									title={__("Post Query", "ananta-sites")} 
									opened={activeGeneralPanel === 'postQuery'}
									onToggle={() => toggleGeneralPanel('postQuery')}
									>
										{queryControlPanel}
										<BaseControl label={__('Filter by Category')} className="ananta-control-label ananta-font-family-picker">	
											<Select2
												value={
													categoryArray.length > 0
														? JSON.parse(categoryArray)
														: ""
												}
												onChange={(selected) =>
													setAttributes({
														categoryArray: JSON.stringify(
															selected
														),
													})
												}
												options={[...categoriesList.map(p => ({
													value: p.id,
													label: p.name,
												}))]}
												isMulti="true"
											/>
										</BaseControl>
										<BaseControl label={__('Authors')} className="ananta-control-label ananta-font-family-picker">		
											<Select2
												value={
													authorArray.length > 0
														? JSON.parse(authorArray)
														: ""
												}
												onChange={(selected) =>
													setAttributes({
														authorArray: JSON.stringify(
															selected
														),
													})
												}
												options={[...authorsList.map(p => ({
													value: p.id,
													label: p.name,
												}))]}
												isMulti="true"
											/>
										</BaseControl>
										<BaseControl label={__('Filter By Tags')} className="ananta-control-label ananta-font-family-picker">
											<Select2
												value={
													tagArray.length > 0
														? JSON.parse(tagArray)
														: ""
												}
												onChange={(selected) =>
													setAttributes({
														tagArray: JSON.stringify(
															selected
														),
													})
												}
												options={[...tagsList.map(p => ({
													value: p.id,
													label: p.name,
													}))]}
												isMulti="true"
											/>
										</BaseControl>
									</PanelBody>
									</>
									}
									{ 
									"style" === tab.name && 
									<>
									<PanelBody 
									title={__("Post Style", "ananta-sites")} 
									opened={activeStylePanel === 'postStyle'}
									onToggle={() => toggleStylePanel('postStyle')} 
									>
										<BaseControl className="ananta-res-choose-control" >
										<div className='ananta-res-choose-controls'>
											<div className='ananta-res-choose-label'>
												{__("Content Position", "ananta-sites")}
											</div>
										</div>
											<div className='ananta-res-choose-toggle-container'>
												<ToggleGroupControl 
													value={ contentPosition }
													onChange={(value) => setAttributes({ contentPosition: value })}
													isBlock 
												>

													<ToggleGroupControlOption
														aria-label="Top"
														label="Top"
														showTooltip
														value="flex-start"
													/>
													<ToggleGroupControlOption
														aria-label="Middle"
														label="Middle"
														showTooltip
														value="center"
													/>
													<ToggleGroupControlOption
														aria-label="Bottom"
														label="Bottom"
														showTooltip
														value="flex-end"
													/>

												</ToggleGroupControl>
											</div>
										</BaseControl>
										<TabPanel
											className="ananta-tab-panel"
											activeClass="tab-active"
											onSelect={ onSelect }
											tabs={ [
												{
													name: 'normal',
													title: 'Normal',
													className: 'ananta-button normal',
												},
												{
													name: 'hover',
													title: 'Hover',
													className: 'ananta-button hover',
												},
											] }
										>
										{ ( tab ) =>
											<div className= {"ananta-tab-controls " + tab.name }>
											{ 
											"normal" === tab.name && 
											<>
												<ColorPickerControl
														values={postBgColor}
														onChange={(value) => {
															setAttributes({
																postBgColor: { ...value },
															});
														}}
														label="Background Color"
												/>
												<CustomBorderControl
													values={postBorderType}
													onChange={(value) => {
														setAttributes({
															postBorderType: { ...value },
														});
													}}
												/>
												<ResponsiveDimentionControl
													multinum={postBorderRadius}
													onChange={(value) => {
														setAttributes({
															postBorderRadius: { ...value },
														});
													}}
													label="Border Radius"
												/>
												<ResponsiveDimentionControl
													multinum={postPadding}
													onChange={(value) => {
														setAttributes({
															postPadding: { ...value },
														});
													}}
													label="Padding"
												/>
												<ResponsiveDimentionControl
													multinum={postInnerPadding}
													onChange={(value) => {
														setAttributes({
															postInnerPadding: { ...value },
														});
													}}
													label="Inner Padding"
												/>
												<BoxShadowControl
													shadow={postShadow}
													onChange={(value) => {
														setAttributes({
															postShadow: { ...value },
														});
													}}
												/>
											</>
											}
											{ 
											"hover" === tab.name && 
											<>
												<ColorPickerControl
														values={postBgHoverColor}
														onChange={(value) => {
															setAttributes({
																postBgHoverColor: { ...value },
															});
														}}
														label="Background Color"
												/>
												<CustomBorderControl
													values={postHoverBorderType}
													onChange={(value) => {
														setAttributes({
															postHoverBorderType: { ...value },
														});
													}}
												/>
												<ResponsiveDimentionControl
													multinum={postHoverBorderRadius}
													onChange={(value) => {
														setAttributes({
															postHoverBorderRadius: { ...value },
														});
													}}
													label="Border Radius"
												/>
												<ResponsiveDimentionControl
													multinum={postHoverPadding}
													onChange={(value) => {
														setAttributes({
															postHoverPadding: { ...value },
														});
													}}
													label="Padding"
												/>
												<ResponsiveDimentionControl
													multinum={postInnerHoverPadding}
													onChange={(value) => {
														setAttributes({
															postInnerHoverPadding: { ...value },
														});
													}}
													label="Inner Padding"
												/>
												<BoxShadowControl
													shadow={postHoverShadow}
													onChange={(value) => {
														setAttributes({
															postHoverShadow: { ...value },
														});
													}}
												/>
											</>
											}
											</div>
										}
										</TabPanel>
									</PanelBody>
									<PanelBody 
									title={__("Image Style", "ananta-sites")} 
									opened={activeStylePanel === 'imgStyle'}
									onToggle={() => toggleStylePanel('imgStyle')}  
									>
										<ResponsiveRangeControl
											values={postLargeImageHeight}
											onChange={(value) => {
												setAttributes({
													postLargeImageHeight: { ...value },
												});
											}}
											label="Large Image Height"
											min={0}
											max={1000}
										/>
										<ResponsiveRangeControl
											values={postSmallImageHeight}
											onChange={(value) => {
												setAttributes({
													postSmallImageHeight: { ...value },
												});
											}}
											label="Small Image Height"
											min={0}
											max={1000}
										/>
										<TabPanel
											className="ananta-tab-panel"
											activeClass="tab-active"
											onSelect={ onSelect }
											tabs={ [
												{
													name: 'normal',
													title: 'Normal',
													className: 'ananta-button normal',
												},
												{
													name: 'hover',
													title: 'Hover',
													className: 'ananta-button hover',
												},
											] }
										>
										{ ( tab ) =>
											<div className= {"ananta-tab-controls " + tab.name }>
											{ 
											"normal" === tab.name && 
											<> 
												<CustomGradientControl 
													values={overlayColor}
													onChange={(value) => {
														setAttributes({
															overlayColor: { ...value },
														});
													}}
													label="Overlay Color" 
												/>
												<RangeControl
													className='ananta-range-label'
													label={__("Opacity", "ananta-sites")}
													value={ overlayOpacity }
													onChange={(value) => setAttributes({ overlayOpacity: value })}
													min={0}
													max={1}
													step={0.01}
												/>
												<CustomBorderControl
													values={postImageBorderType}
													onChange={(value) => {
														setAttributes({
															postImageBorderType: { ...value },
														});
													}}
												/>
												<ResponsiveDimentionControl
													multinum={postImageBorderRadius}
													onChange={(value) => {
														setAttributes({
															postImageBorderRadius: { ...value },
														});
													}}
													label="Border Radius"
												/>
											</>
											}
											{ 
											"hover" === tab.name && 
											<>
												<CustomBorderControl
													values={postImageHoverBorderType}
													onChange={(value) => {
														setAttributes({
															postImageHoverBorderType: { ...value },
														});
													}}
												/>
												<ResponsiveDimentionControl
													multinum={postImageHoverBorderRadius}
													onChange={(value) => {
														setAttributes({
															postImageHoverBorderRadius: { ...value },
														});
													}}
													label="Border Radius"
												/>
											</>
											}
											</div>
										}
										</TabPanel>
									</PanelBody>
									<PanelBody 
									title={__("Category Style", "ananta-sites")} 
									opened={activeStylePanel === 'categoryStyle'}
									onToggle={() => toggleStylePanel('categoryStyle')} 
									>
										<div className='ananta-select-control'>
											<label for="ananta-select-control-label" className='ananta-select-control-label'>{__("Position", "ananta-sites")}</label>
											<SelectControl
													value={ categoryPosition }
													options={ [
														{ label: 'Above Title', value: 'category-bottom' },
														{ label: 'Top Left', value: 'category-top-left' },
														{ label: 'Top Right', value: 'category-top-right' },
													] }
													onChange={ ( value ) => { setAttributes( { categoryPosition: value } );}}
											/>
										</div>
										{categoryPosition === 'category-bottom' && (
											<ResponsiveAlignControl 
												choose={categoryAlignment}
												onChange={(value) => {
													setAttributes({
														categoryAlignment: { ...value },
													});
												}}
												label="Alignment" 
											/>
										)}
										{categoryPosition === 'category-top-right' && (
											<ResponsiveRangeControl
												values={categoryPositionRight}
												onChange={(value) => {
													setAttributes({
														categoryPositionRight: { ...value },
													});
												}}
												label="Right Space"
												min={0}
												max={150}
											/>
										)}
										{categoryPosition === 'category-top-left' && (
											<ResponsiveRangeControl
												values={categoryPositionLeft}
												onChange={(value) => {
													setAttributes({
														categoryPositionLeft: { ...value },
													});
												}}
												label="Left Space"
												min={0}
												max={150}
											/>
										)}
										<TabPanel
											className="ananta-tab-panel"
											activeClass="tab-active"
											onSelect={ onSelect }
											tabs={ [
												{
													name: 'normal',
													title: 'Normal',
													className: 'ananta-button normal',
												},
												{
													name: 'hover',
													title: 'Hover',
													className: 'ananta-button hover',
												},
											] }
										>
										{ ( tab ) =>
											<div className= {"ananta-tab-controls " + tab.name }>
											{ 
											"normal" === tab.name && 
											<>
												<ColorPickerControl
														values={categoryBgColor}
														onChange={(value) => {
															setAttributes({
																categoryBgColor: { ...value },
															});
														}}
														label="Background Color"
												/>
												<ColorPickerControl
														values={categoryColor}
														onChange={(value) => {
															setAttributes({
																categoryColor: { ...value },
															});
														}}
														label="Color"
												/>
												<TypographyControl
													typography={categoryTypography}
													onChange={(value) => {
														setAttributes({
															categoryTypography: { ...value },
														});
													}}
													label="Typography"
												/>
												<CustomBorderControl
													values={categoryBorderType}
													onChange={(value) => {
														setAttributes({
															categoryBorderType: { ...value },
														});
													}}
												/>
												<ResponsiveDimentionControl
													multinum={categoryBorderRadius}
													onChange={(value) => {
														setAttributes({
															categoryBorderRadius: { ...value },
														});
													}}
													label="Border Radius"
												/>
												<ResponsiveDimentionControl
													multinum={categoryPadding}
													onChange={(value) => {
														setAttributes({
															categoryPadding: { ...value },
														});
													}}
													label="Padding"
												/>
												<ResponsiveDimentionControl
													multinum={categoryMargin}
													onChange={(value) => {
														setAttributes({
															categoryMargin: { ...value },
														});
													}}
													label="Margin"
												/>
												<BoxShadowControl
													shadow={categoryShadow}
													onChange={(value) => {
														setAttributes({
															categoryShadow: { ...value },
														});
													}}
												/>
											</>
											}
											{ 
											"hover" === tab.name && 
											<>
												<ColorPickerControl
													values={categoryBgHoverColor}
													onChange={(value) => {
														setAttributes({
															categoryBgHoverColor: { ...value },
														});
													}}
													label="Background Color"
												/>
												<ColorPickerControl
													values={categoryHoverColor}
													onChange={(value) => {
														setAttributes({
															categoryHoverColor: { ...value },
														});
													}}
													label="Color"
												/>
												<CustomBorderControl
													values={categoryHoverBorderType}
													onChange={(value) => {
														setAttributes({
															categoryHoverBorderType: { ...value },
														});
													}}
												/>
												<ResponsiveDimentionControl
													multinum={categoryHoverBorderRadius}
													onChange={(value) => {
														setAttributes({
															categoryHoverBorderRadius: { ...value },
														});
													}}
													label="Border Radius"
												/>
												<ResponsiveDimentionControl
													multinum={categoryHoverPadding}
													onChange={(value) => {
														setAttributes({
															categoryHoverPadding: { ...value },
														});
													}}
													label="Padding"
												/>
												<ResponsiveDimentionControl
													multinum={categoryHoverMargin}
													onChange={(value) => {
														setAttributes({
															categoryHoverMargin: { ...value },
														});
													}}
													label="Margin"
												/>
												<BoxShadowControl
													shadow={categoryHoverShadow}
													onChange={(value) => {
														setAttributes({
															categoryHoverShadow: { ...value },
														});
													}}
												/>
											</>
											}
											</div>
										}
										</TabPanel>
									</PanelBody>
									<PanelBody 
									title={__("Title Style", "ananta-sites")} 
									opened={activeStylePanel === 'titleStyle'}
									onToggle={() => toggleStylePanel('titleStyle')}   
									>
										<ResponsiveAlignControl 
											choose={titleAlignment}
											onChange={(value) => {
												setAttributes({
													titleAlignment: { ...value },
												});
											}}
											label="Alignment" 
										/>
										<TabPanel
											className="ananta-tab-panel"
											activeClass="tab-active"
											onSelect={ onSelect }
											tabs={ [
												{
													name: 'normal',
													title: 'Normal',
													className: 'ananta-button normal',
												},
												{
													name: 'hover',
													title: 'Hover',
													className: 'ananta-button hover',
												},
											] }
										>
										{ ( tab ) =>
											<div className= {"ananta-tab-controls " + tab.name }>
											{ 
											"normal" === tab.name && 
											<>
												<ColorPickerControl
													className="ananta-color-style"
													values={titleColor}
													onChange={(value) => {
														setAttributes({
															titleColor: { ...value },
														});
													}}
													label="Color"
												/>
												<TypographyControl
													typography={titleTypography}
													onChange={(value) => {
														setAttributes({
															titleTypography: { ...value },
														});
													}}
													label="Typography Large"
												/>
												<TypographyControl
													typography={titleTypographySmall}
													onChange={(value) => {
														setAttributes({
															titleTypographySmall: { ...value },
														});
													}}
													label="Typography Small"
												/>
												<ResponsiveDimentionControl
													multinum={titleMargin}
													onChange={(value) => {
														setAttributes({
															titleMargin: { ...value },
														});
													}}
													label="Margin"
												/>
											</>
											}
											{ 
											"hover" === tab.name && 
											<>
												<ColorPickerControl
													className="ananta-color-style"
													values={titleHoverColor}
													onChange={(value) => {
														setAttributes({
															titleHoverColor: { ...value },
														});
													}}
													label="Color"
												/>
												<ResponsiveDimentionControl
													multinum={titleHoverMargin}
													onChange={(value) => {
														setAttributes({
															titleHoverMargin: { ...value },
														});
													}}
													label="Margin"
												/>
											</>
											}
												</div>
										}
										</TabPanel>
									</PanelBody>
									<PanelBody 
									title={__("Metas Style", "ananta-sites")} 
									opened={activeStylePanel === 'metaStyle'}
									onToggle={() => toggleStylePanel('metaStyle')}   
									>
										<ResponsiveAlignControl 
											choose={metaAlignment}
											onChange={(value) => {
												setAttributes({
													metaAlignment: { ...value },
												});
											}}
											label="Alignment" 
										/>
										<TabPanel
											className="ananta-tab-panel"
											activeClass="tab-active"
											onSelect={ onSelect }
											tabs={ [
												{
													name: 'normal',
													title: 'Normal',
													className: 'ananta-button normal',
												},
												{
													name: 'hover',
													title: 'Hover',
													className: 'ananta-button hover',
												},
											] }
										>
										{ ( tab ) =>
											<div className= {"ananta-tab-controls " + tab.name }>
											{ 
											"normal" === tab.name && 
											<>
												<ColorPickerControl
													className="ananta-color-style"
													values={metaColor}
													onChange={(value) => {
														setAttributes({
															metaColor: { ...value },
														});
													}}
													label="Color"
												/>
												<ColorPickerControl
													className="ananta-color-style"
													values={metaIconColor}
													onChange={(value) => {
														setAttributes({
															metaIconColor: { ...value },
														});
													}}
													label="Icon Color"
												/>
												<TypographyControl
													typography={metaTypography}
													onChange={(value) => {
														setAttributes({
															metaTypography: { ...value },
														});
													}}
													label="Typography"
												/>
												<ResponsiveDimentionControl
													multinum={metaMargin}
													onChange={(value) => {
														setAttributes({
															metaMargin: { ...value },
														});
													}}
													label="Margin"
												/>
											</>
											}
											{ 
											"hover" === tab.name && 
											<>
												<ColorPickerControl
													className="ananta-color-style"
													values={metaHoverColor}
													onChange={(value) => {
														setAttributes({
															metaHoverColor: { ...value },
														});
													}}
													label="Color"
												/>
												<ColorPickerControl
													className="ananta-color-style"
													values={metaIconHoverColor}
													onChange={(value) => {
														setAttributes({
															metaIconHoverColor: { ...value },
														});
													}}
													label="Icon Color"
												/>
												<ResponsiveDimentionControl
													multinum={metaHoverMargin}
													onChange={(value) => {
														setAttributes({
															metaHoverMargin: { ...value },
														});
													}}
													label="Margin"
												/>
											</>
											}
												</div>
										}
										</TabPanel>
									</PanelBody>
									</>
									}
									{ 
									"advanced" === tab.name && 
									<>
									<PanelBody
										title={__("Responsive", "ananta-sites")}
										opened={activeAdvancedPanel === 'responsive'}
										onToggle={() => toggleAdvancedPanel('responsive')}
									>
										<ToggleControl
											className="ananta-control-label"
											label={__("Hide Desktop", "ananta-sites")}
											checked={hideDesktop}
											onChange={(hideDesktop) => setAttributes({ hideDesktop })}
										/>
										<ToggleControl
											className="ananta-control-label"
											label={__("Hide Tablet", "ananta-sites")}
											checked={hideTablet}
											onChange={(hideTablet) => setAttributes({ hideTablet })}
										/>
										<ToggleControl
											className="ananta-control-label"
											label={__("Hide Mobile", "ananta-sites")}
											checked={hideMobile}
											onChange={(hideMobile) => setAttributes({ hideMobile })}
										/>
									</PanelBody>
									</>
									}
								</div> 
							}
						</TabPanel>
		</div>
    </InspectorControls>
)
}